/**
 * @param {string} path
 * @returns {Boolean}
 */
export function isExternal(path) {
  return /^(https?:|mailto:|tel:)/.test(path)
}

/**
 * @param {string} str
 * @returns {Boolean}
 */
export function validUsername(str) {
  const valid_map = ['admin', 'editor']
  return valid_map.indexOf(str.trim()) >= 0
}

/**
 * @param {string} url
 * @returns {Boolean}
 */
export function validURL(url) {
  const reg = /^(https?|ftp):\/\/([a-zA-Z0-9.-]+(:[a-zA-Z0-9.&%$-]+)*@)*((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[1-9][0-9]?)(\.(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[1-9]?[0-9])){3}|([a-zA-Z0-9-]+\.)*[a-zA-Z0-9-]+\.(com|edu|gov|int|mil|net|org|biz|arpa|info|name|pro|aero|coop|museum|[a-zA-Z]{2}))(:[0-9]+)*(\/($|[a-zA-Z0-9.,?'\\+&%$#=~_-]+))*$/
  return reg.test(url)
}

/**
 * @param {string} str
 * @returns {Boolean}
 */
export function validLowerCase(str) {
  const reg = /^[a-z]+$/
  return reg.test(str)
}

/**
 * @param {string} str
 * @returns {Boolean}
 */
export function validUpperCase(str) {
  const reg = /^[A-Z]+$/
  return reg.test(str)
}

/**
 * @param {string} str
 * @returns {Boolean}
 */
export function validAlphabets(str) {
  const reg = /^[A-Za-z]+$/
  return reg.test(str)
}

/**
 * @param {string} email
 * @returns {Boolean}
 */
export function validEmail(email) {
  const reg = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
  return reg.test(email)
}

/**
 * @param {string} str
 * @returns {Boolean}
 */
export function isString(str) {
  if (typeof str === 'string' || str instanceof String) {
    return true
  }
  return false
}

/**
 * @param {Array} arg
 * @returns {Boolean}
 */
export function isArray(arg) {
  if (typeof Array.isArray === 'undefined') {
    return Object.prototype.toString.call(arg) === '[object Array]'
  }
  return Array.isArray(arg)
}

/**
 * @param date new Date
 * @returns {string}
 */
export function formatTime(date) {

  const formatNumber = n => {
    n = n.toString()
    return n[1] ? n : '0' + n
  }

  const year = date.getFullYear()
  const month = date.getMonth() + 1
  const day = date.getDate()
  const hour = date.getHours()
  const minute = date.getMinutes()
  const second = date.getSeconds()

  return [year, month, day].map(formatNumber).join('-') + ' ' + [hour, minute, second].map(formatNumber).join(':')
}

export function figureUsage(date) {
  const abortDate = new Date(date).getTime()
  const beginDate = new Date().getTime()
  if (abortDate - beginDate < 0) {
    return 0
  } else if (abortDate - beginDate > 0 && abortDate - beginDate < 604800000) {
    return 1
  } else {
    return 2
  }
}

/**
 * encrypt mobile phone& idCard
 * @param data
 * @param dataType
 * @returns {*}
 */
export function secretData(data, dataType) {

  if (dataType == 'phone') {
    //手机号码星号转化
    var tel = data;
    var reg = /^(\d{3})\d{4}(\d{4})$/;
    if (tel) {
      tel = tel.replace(reg, "$1****$2");
      return tel;
    }
    ;
  } else if (dataType == 'idCard') {
    //身份证号星号转化
    var idCard = data;
    if (idCard.length == 18) {
      idCard = idCard.substring(0, 14) + '****';
      return idCard;
    } else {
      idCard = idCard.substring(0, 11) + '****';
      return idCard;
    }
  } else if (dataType === 'num') {
    var str = data;
    if (str.length > 11) {
      str = str.slice(0, 5) + '*****' + str.slice(-5);
    }
    return str;
  }
}

export function identityCodeValid(identityCard) {
  let city = {
    11: "北京",
    12: "天津",
    13: "河北",
    14: "山西",
    15: "内蒙古",
    21: "辽宁",
    22: "吉林",
    23: "黑龙江 ",
    31: "上海",
    32: "江苏",
    33: "浙江",
    34: "安徽",
    35: "福建",
    36: "江西",
    37: "山东",
    41: "河南",
    42: "湖北 ",
    43: "湖南",
    44: "广东",
    45: "广西",
    46: "海南",
    50: "重庆",
    51: "四川",
    52: "贵州",
    53: "云南",
    54: "西藏 ",
    61: "陕西",
    62: "甘肃",
    63: "青海",
    64: "宁夏",
    65: "新疆",
    71: "台湾",
    81: "香港",
    82: "澳门",
    91: "国外 "
  };
  let tip = "";
  let pass = true;
  let moreInfo = {};
  let reg = /(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)|(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$)/;
  if (!identityCard || !reg.test(identityCard)) {
    tip = "身份证号格式错误";
    pass = false;
  } else if (!city[identityCard.substr(0, 2)]) {
    tip = "地址编码错误";
    pass = false;
  } else {
    //18位身份证需要验证最后一位校验位
    if (identityCard.length == 18) {
      identityCard = identityCard.split('');
      //∑(ai×Wi)(mod 11)
      //加权因子
      let factor = [7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2];
      //校验位
      let parity = [1, 0, 'X', 9, 8, 7, 6, 5, 4, 3, 2];
      let sum = 0;
      let ai = 0;
      let wi = 0;
      for (let i = 0; i < 17; i++) {
        ai = identityCard[i];
        wi = factor[i];
        sum += ai * wi;
      }
      let last = parity[sum % 11];
      if (parity[sum % 11] != identityCard[17]) {
        tip = "校验位错误";
        pass = false;
      }
    }
  }
  if (pass) {
    let len = identityCard.length;
    let code = identityCard.join('');
    let strBirthday = "";

    //年龄
    if (len == 18)//处理18位的身份证号码从号码中得到生日和性别代码
    {
      strBirthday = code.substr(6, 4) + "/" + code.substr(10, 2) + "/" + code.substr(12, 2);
    } else if (len == 15) {
      strBirthday = "19" + code.substr(6, 2) + "/" + code.substr(8, 2) + "/" + code.substr(10, 2);
    }
    //时间字符串里，必须是“/”
    let birthDate = new Date(strBirthday);
    let nowDateTime = new Date();
    moreInfo.age = nowDateTime.getFullYear() - birthDate.getFullYear();
    //再考虑月、天的因素;.getMonth()获取的是从0开始的，这里进行比较，不需要加1
    //if (nowDateTime.getMonth() < birthDate.getMonth() || (nowDateTime.getMonth() == birthDate.getMonth() && nowDateTime.getDate() < birthDate.getDate())) {
    //    moreInfo.age--;
    //}
    moreInfo.birthDate = birthDate.getFullYear() + '-' + ((birthDate.getMonth() + 1) > 9 ? (birthDate.getMonth() + 1) : ('0' + (birthDate.getMonth() + 1))) + '-' + (birthDate.getDate() > 9 ? birthDate.getDate() : ('0' + birthDate.getDate()));
    //性别
    if (parseInt(code.substr(16, 1)) % 2 == 1) {
      moreInfo.sex = 0; // 男
    } else {
      moreInfo.sex = 1; // 女
    }
  }

  //if(!pass) alert(tip);
  return {
    'pass': pass,
    'tip': tip,
    'more': moreInfo
  };
}

export function identityPhone(phone) {
  let reg = /^1[34578]\d{9}$/;
  return reg.test(phone)
}



